<template>
<div>
  <div>
    you dapp have {{mytokenNum}}
  </div>
  <div class="grid grid-cols-4 grid-rows-6 gap-x-6 gap-y-7">
    <div v-for="sit in mysite" class="card text-center shadow-2xl" @click="goto_change(sit)">
      <figure class="px-3 pt-3">
        <img src="sit.logoimg" class="rounded-xl">
      </figure>
      <div class="card-body">
        <h2 class="card-title">sit.name</h2>
        <p>sit.description</p>
        <div class="justify-center card-actions">
          <button class="btn btn-outline btn-accent" @click="gotodapp(sit.url)">Get Start</button>
        </div>
      </div>
    </div>
  </div>

</div>
</template>

<script>
import {mapState} from "vuex"

export default {
  name: "me",
  data(){
    return {
      mytokenNum:0
    }
  },
  computed:{
    ...mapState([
        "contract",
        "userAddress",
        "mysite"
    ])
  },
  methods:{
    goto_change(sit){
      this.$router.push({path:"/change_site",query:{
          sitid:sit.sitid,
          // sitid:101,
          url:sit.url,
          logoimg:sit.logoimg,
          name:sit.name,
          description:sit.description
      }})
    }
  },
  mounted:async function(){
    this.mytokenNum=await this.contract.balanceOf(this.userAddress)
    let count=100

    if(this.mysite==0){

      while(true){
        count+=1
        let tmp=null
        try{
          tmp=await this.contract.ownerOf(count)
        }catch {
          console.log("超出了范围！！！")
          break
        }

        console.log(tmp)
        if(tmp==this.userAddress){
          let site={}
          let result=await this.contract.TokenURI(count)
          site["sitid"]=count
          site["logoimg"]=result[0]
          site["url"]=result[1]
          site["name"]=result[2]
          site["description"]=result[3]
          console.log(result,typeof result,result[0],result[1],result[2],result[3])
          this.$store.dispatch("addMySite",{
            Site:site
          })
          console.log(site)
        }
      }

    }
  }
}
</script>

